// frontend/src/components/social/SocialConnector.jsx
import React, { useState, useEffect } from 'react';
import apiService from '../../services/api';

/**
 * Componente para manejar la conexión con plataformas sociales
 * Soporta varios tipos de autenticación OAuth según la plataforma
 */
const SocialConnector = ({ platform, onConnect, onDisconnect }) => {
  const [isConnecting, setIsConnecting] = useState(false);
  const [isConnected, setIsConnected] = useState(false);
  const [accountInfo, setAccountInfo] = useState(null);
  const [error, setError] = useState(null);
  
  // Configuración de OAuth para cada plataforma
  const platformConfig = {
    instagram: {
      name: 'Instagram',
      icon: 'instagram',
      color: '#E1306C',
      authUrl: 'https://api.instagram.com/oauth/authorize',
      clientId: process.env.REACT_APP_INSTAGRAM_CLIENT_ID || 'YOUR_INSTAGRAM_CLIENT_ID',
      redirectUri: `${window.location.origin}/social/callback/instagram`,
      scope: 'user_profile,user_media',
    },
    github: {
      name: 'GitHub',
      icon: 'github',
      color: '#333',
      authUrl: 'https://github.com/login/oauth/authorize',
      clientId: process.env.REACT_APP_GITHUB_CLIENT_ID || 'YOUR_GITHUB_CLIENT_ID',
      redirectUri: `${window.location.origin}/social/callback/github`,
      scope: 'repo',
    },
    reddit: {
      name: 'Reddit',
      icon: 'reddit',
      color: '#FF4500',
      authUrl: 'https://www.reddit.com/api/v1/authorize',
      clientId: process.env.REACT_APP_REDDIT_CLIENT_ID || 'YOUR_REDDIT_CLIENT_ID',
      redirectUri: `${window.location.origin}/social/callback/reddit`,
      scope: 'identity,submit',
      duration: 'permanent',
      responseType: 'code',
    },
    stackoverflow: {
      name: 'Stack Overflow',
      icon: 'stack-overflow',
      color: '#F48024',
      authUrl: 'https://stackoverflow.com/oauth',
      clientId: process.env.REACT_APP_STACKOVERFLOW_CLIENT_ID || 'YOUR_STACKOVERFLOW_CLIENT_ID',
      redirectUri: `${window.location.origin}/social/callback/stackoverflow`,
      scope: 'no_expiry',
    },
    telegram: {
      name: 'Telegram',
      icon: 'telegram',
      color: '#0088cc',
      // Telegram utiliza un método diferente para bots - botfather
      isBot: true,
    },
    threads: {
      name: 'Threads',
      icon: 'at',
      color: '#000000',
      // Threads utiliza la API de Instagram
      useInstagram: true,
    },
    tiktok: {
      name: 'TikTok',
      icon: 'video',
      color: '#000000',
      authUrl: 'https://open-api.tiktok.com/platform/oauth/connect/',
      clientId: process.env.REACT_APP_TIKTOK_CLIENT_ID || 'YOUR_TIKTOK_CLIENT_ID',
      redirectUri: `${window.location.origin}/social/callback/tiktok`,
      scope: 'user.info.basic,video.publish',
      responseType: 'code',
    },
    csdn: {
      name: 'CSDN',
      icon: 'code',
      color: '#CA0C16',
      // CSDN podría requerir implementación específica para el mercado chino
      customAuth: true,
    },
  };
  
  const config = platformConfig[platform];
  
  // Verificar si ya está conectada la cuenta
  useEffect(() => {
    const checkConnection = async () => {
      try {
        const accounts = await apiService.social.getAccounts(platform);
        
        if (accounts && accounts.length > 0) {
          setIsConnected(true);
          setAccountInfo(accounts[0]);
        }
      } catch (error) {
        console.error('Error checking connection:', error);
      }
    };
    
    checkConnection();
  }, [platform]);
  
  // Función para iniciar el proceso de OAuth
  const startOAuthFlow = () => {
    setIsConnecting(true);
    setError(null);
    
    // Handle especial para Telegram (bot)
    if (config.isBot) {
      // Mostrar modal para input de token de bot
      showBotTokenInput();
      return;
    }
    
    // Handle especial para Threads (usa Instagram)
    if (config.useInstagram) {
      // Usar flujo de Instagram
      const instagramConfig = platformConfig.instagram;
      const authUrl = buildOAuthUrl(instagramConfig);
      window.location.href = authUrl;
      return;
    }
    
    // Handle especial para CSDN
    if (config.customAuth) {
      // Mostrar modal para autenticación manual
      showManualAuthInput();
      return;
    }
    
    // Flujo OAuth estándar para las demás plataformas
    const authUrl = buildOAuthUrl(config);
    window.location.href = authUrl;
  };
  
  // Construir URL de OAuth
  const buildOAuthUrl = (config) => {
    const params = new URLSearchParams({
      client_id: config.clientId,
      redirect_uri: config.redirectUri,
      scope: config.scope,
      response_type: config.responseType || 'code',
      state: generateState(),
    });
    
    if (config.duration) {
      params.append('duration', config.duration);
    }
    
    return `${config.authUrl}?${params.toString()}`;
  };
  
  // Generar estado para seguridad
  const generateState = () => {
    const state = Math.random().toString(36).substring(2);
    localStorage.setItem('oauth_state', state);
    return state;
  };
  
  // Mostrar input para token de bot (Telegram)
  const showBotTokenInput = () => {
    // En una implementación real, esto sería un modal React
    const botToken = prompt(`Por favor, ingresa el token de tu bot de ${config.name}:`);
    
    if (botToken) {
      connectWithBotToken(botToken);
    } else {
      setIsConnecting(false);
    }
  };
  
  // Conectar con token de bot (Telegram)
  const connectWithBotToken = async (token) => {
    try {
      const response = await apiService.social.createAccount({
        platform: platform,
        account_name: 'Bot',
        auth_token: token,
        active: true,
      });
      
      setIsConnected(true);
      setAccountInfo(response);
      onConnect && onConnect(response);
    } catch (error) {
      setError('Error al conectar con token de bot');
    } finally {
      setIsConnecting(false);
    }
  };
  
  // Mostrar input para autenticación manual (CSDN)
  const showManualAuthInput = () => {
    // En una implementación real, esto sería un modal React
    const apiKey = prompt(`Por favor, ingresa tu API key de ${config.name}:`);
    const apiSecret = prompt(`Por favor, ingresa tu API secret de ${config.name}:`);
    
    if (apiKey && apiSecret) {
      connectWithManualAuth(apiKey, apiSecret);
    } else {
      setIsConnecting(false);
    }
  };
  
  // Conectar con autenticación manual (CSDN)
  const connectWithManualAuth = async (apiKey, apiSecret) => {
    try {
      const response = await apiService.social.createAccount({
        platform: platform,
        account_name: 'Manual Account',
        auth_token: apiKey,
        refresh_token: apiSecret,
        active: true,
      });
      
      setIsConnected(true);
      setAccountInfo(response);
      onConnect && onConnect(response);
    } catch (error) {
      setError('Error al conectar con autenticación manual');
    } finally {
      setIsConnecting(false);
    }
  };
  
  // Desconectar la cuenta
  const handleDisconnect = async () => {
    if (!accountInfo) return;
    
    try {
      // En una implementación real, debería haber un endpoint para desactivar la cuenta
      await apiService.social.updateAccount(accountInfo.id, { active: false });
      
      setIsConnected(false);
      setAccountInfo(null);
      onDisconnect && onDisconnect();
    } catch (error) {
      setError('Error al desconectar la cuenta');
    }
  };
  
  return (
    <div className={`social-connector social-connector-${platform}`}>
      <div className="connector-header" style={{ backgroundColor: config.color }}>
        <i className={`fab fa-${config.icon}`}></i>
        <span>{config.name}</span>
      </div>
      
      <div className="connector-body">
        {isConnected ? (
          <>
            <div className="connector-status connected">
              <i className="fas fa-check-circle"></i>
              <span>Conectado</span>
            </div>
            
            <div className="account-info">
              <p>Cuenta: {accountInfo?.account_name || 'Sin nombre'}</p>
            </div>
            
            <button 
              className="disconnect-button"
              onClick={handleDisconnect}
              disabled={isConnecting}
            >
              Desconectar
            </button>
          </>
        ) : (
          <>
            <div className="connector-status">
              <span>No conectado</span>
            </div>
            
            {error && (
              <div className="connector-error">
                {error}
              </div>
            )}
            
            <button 
              className="connect-button"
              onClick={startOAuthFlow}
              disabled={isConnecting}
            >
              {isConnecting ? 'Conectando...' : `Conectar con ${config.name}`}
            </button>
          </>
        )}
      </div>
    </div>
  );
};

export default SocialConnector;
