// frontend/src/contexts/ToastContext.js
import React, { createContext, useState, useContext } from 'react';
import Toast from '../components/ui/Toast';
import { v4 as uuidv4 } from 'uuid'; // Necesitamos añadir esta dependencia

// Crear contexto
export const ToastContext = createContext();

/**
 * Proveedor de contexto para toasts
 * Permite mostrar toasts desde cualquier parte de la aplicación
 */
export const ToastProvider = ({ children }) => {
  const [toasts, setToasts] = useState([]);
  
  /**
   * Mostrar un nuevo toast
   * @param {string} message - Mensaje a mostrar
   * @param {string} type - Tipo de toast (success, error, warning, info)
   * @param {number} duration - Duración en ms
   */
  const showToast = (message, type = 'info', duration = 3000) => {
    const id = uuidv4();
    setToasts(prevToasts => [...prevToasts, { id, message, type, duration }]);
    
    // Registrar en analytics si es relevante
    if (type === 'error') {
      // Podríamos registrar errores importantes para seguimiento
      console.error(`[Toast Error]: ${message}`);
    }
    
    return id; // Devolver ID por si se quiere cerrar manualmente
  };
  
  /**
   * Ocultar un toast específico
   * @param {string} id - ID del toast a ocultar
   */
  const hideToast = (id) => {
    setToasts(prevToasts => prevToasts.filter(toast => toast.id !== id));
  };
  
  /**
   * Helpers para tipos específicos de toast
   */
  const showSuccess = (message, duration) => showToast(message, 'success', duration);
  const showError = (message, duration) => showToast(message, 'error', duration);
  const showWarning = (message, duration) => showToast(message, 'warning', duration);
  const showInfo = (message, duration) => showToast(message, 'info', duration);
  
  return (
    <ToastContext.Provider value={{ showToast, hideToast, showSuccess, showError, showWarning, showInfo }}>
      {children}
      
      {/* Renderizar toasts */}
      <div className="toast-container">
        {toasts.map(toast => (
          <Toast
            key={toast.id}
            message={toast.message}
            type={toast.type}
            duration={toast.duration}
            onClose={() => hideToast(toast.id)}
          />
        ))}
      </div>
    </ToastContext.Provider>
  );
};

/**
 * Hook para usar el contexto de toasts
 */
export const useToast = () => {
  const context = useContext(ToastContext);
  
  if (!context) {
    throw new Error('useToast debe usarse dentro de un ToastProvider');
  }
  
  return context;
};
