// frontend/src/pages/admin/EarlyAdoptersManager.jsx
import React, { useState, useEffect } from 'react';

const EarlyAdoptersManager = () => {
  const [earlyAdopters, setEarlyAdopters] = useState([]);
  const [selectedAdopter, setSelectedAdopter] = useState(null);
  const [showBadgeModal, setShowBadgeModal] = useState(false);
  const [selectedBadge, setSelectedBadge] = useState(null);
  const [filterSegment, setFilterSegment] = useState('all');
  const [searchQuery, setSearchQuery] = useState('');
  
  const badges = [
    { id: 'pioneer', name: 'Pioneer', description: 'One of the first to join the platform', icon: '🚀' },
    { id: 'innovator', name: 'Innovator', description: 'Contributes creative ideas to the platform', icon: '💡' },
    { id: 'contributor', name: 'Contributor', description: 'Actively contributes to the community', icon: '🛠️' },
    { id: 'visionary', name: 'Visionary', description: 'Helps define the future of the platform', icon: '🔮' },
  ];
  
  useEffect(() => {
    // En una implementación real, cargaríamos los datos desde la API
    // Por ahora, generamos datos de ejemplo
    const generateEarlyAdopters = () => {
      const segments = ['developer', 'business', 'academic', 'ngo_gov'];
      const result = [];
      
      // Generar 30 early adopters aleatorios
      for (let i = 1; i <= 30; i++) {
        const segment = segments[Math.floor(Math.random() * segments.length)];
        const hasBadge = Math.random() > 0.6;
        const badgeType = hasBadge ? badges[Math.floor(Math.random() * badges.length)].id : null;
        
        result.push({
          id: i,
          name: `User ${i}`,
          email: `user${i}@example.com`,
          company: `Company ${i}`,
          segment,
          created_at: new Date(Date.now() - Math.floor(Math.random() * 30) * 24 * 60 * 60 * 1000).toISOString(),
          has_badge: hasBadge,
          badge_type: badgeType,
          interests: ['identity', 'transparency', 'industrial'].filter(() => Math.random() > 0.5)
        });
      }
      
      setEarlyAdopters(result);
    };
    
    generateEarlyAdopters();
  }, []);
  
  const handleSearch = (e) => {
    setSearchQuery(e.target.value);
  };
  
  const handleFilterChange = (e) => {
    setFilterSegment(e.target.value);
  };
  
  const handleAwardBadge = (adopter) => {
    setSelectedAdopter(adopter);
    setShowBadgeModal(true);
  };
  
  const handleConfirmBadge = () => {
    if (!selectedBadge) return;
    
    // En una implementación real, enviaríamos esto a la API
    // Por ahora, actualizamos el estado local
    setEarlyAdopters(prevAdopters => 
      prevAdopters.map(adopter => 
        adopter.id === selectedAdopter.id
          ? { ...adopter, has_badge: true, badge_type: selectedBadge.id }
          : adopter
      )
    );
    
    setShowBadgeModal(false);
    setSelectedAdopter(null);
    setSelectedBadge(null);
  };
  
  const closeModal = () => {
    setShowBadgeModal(false);
    setSelectedAdopter(null);
    setSelectedBadge(null);
  };
  
  const filteredAdopters = earlyAdopters.filter(adopter => {
    const matchesSegment = filterSegment === 'all' || adopter.segment === filterSegment;
    const matchesSearch = 
      adopter.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      adopter.email.toLowerCase().includes(searchQuery.toLowerCase()) ||
      adopter.company.toLowerCase().includes(searchQuery.toLowerCase());
    
    return matchesSegment && matchesSearch;
  });
  
  const getBadgeById = (badgeId) => {
    return badges.find(badge => badge.id === badgeId) || null;
  };
  
  return (
    <div className="early-adopters-manager">
      <div className="page-header">
        <h1>Early Adopters</h1>
      </div>
      
      <div className="filter-bar">
        <div className="search-input">
          <input 
            type="text" 
            placeholder="Search..." 
            value={searchQuery}
            onChange={handleSearch}
          />
        </div>
        
        <div className="segment-filter">
          <label htmlFor="segment-filter">Filter by segment:</label>
          <select 
            id="segment-filter"
            value={filterSegment}
            onChange={handleFilterChange}
          >
            <option value="all">All Segments</option>
            <option value="developer">Developers</option>
            <option value="business">Business</option>
            <option value="academic">Academic</option>
            <option value="ngo_gov">NGO/Government</option>
          </select>
        </div>
      </div>
      
      <div className="adopters-summary">
        <div className="summary-card">
          <div className="summary-value">{earlyAdopters.length}</div>
          <div className="summary-label">Total Early Adopters</div>
        </div>
        
        <div className="summary-card">
          <div className="summary-value">
            {earlyAdopters.filter(a => a.segment === 'developer').length}
          </div>
          <div className="summary-label">Developers</div>
        </div>
        
        <div className="summary-card">
          <div className="summary-value">
            {earlyAdopters.filter(a => a.segment === 'business').length}
          </div>
          <div className="summary-label">Business</div>
        </div>
        
        <div className="summary-card">
          <div className="summary-value">
            {earlyAdopters.filter(a => a.segment === 'academic').length}
          </div>
          <div className="summary-label">Academic</div>
        </div>
        
        <div className="summary-card">
          <div className="summary-value">
            {earlyAdopters.filter(a => a.segment === 'ngo_gov').length}
          </div>
          <div className="summary-label">NGO/Gov</div>
        </div>
      </div>
      
      <div className="adopters-list">
        <table className="data-table">
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Company</th>
              <th>Segment</th>
              <th>Joined</th>
              <th>Badge</th>
              <th>Interests</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {filteredAdopters.map(adopter => (
              <tr key={adopter.id}>
                <td>{adopter.name}</td>
                <td>{adopter.email}</td>
                <td>{adopter.company || '-'}</td>
                <td>
                  <span className={`segment-badge ${adopter.segment}`}>
                    {adopter.segment}
                  </span>
                </td>
                <td>{new Date(adopter.created_at).toLocaleDateString()}</td>
                <td>
                  {adopter.has_badge ? (
                    <span className={`badge-icon ${adopter.badge_type}`}>
                      {getBadgeById(adopter.badge_type)?.icon} {getBadgeById(adopter.badge_type)?.name}
                    </span>
                  ) : (
                    <span className="no-badge">No badge</span>
                  )}
                </td>
                <td>
                  <div className="interests-list">
                    {adopter.interests.map(interest => (
                      <span key={interest} className={`interest-tag ${interest}`}>
                        {interest}
                      </span>
                    ))}
                  </div>
                </td>
                <td>
                  <button 
                    className="action-button view"
                    onClick={() => alert(`View details for ${adopter.name}`)}
                  >
                    <i className="fas fa-eye"></i>
                  </button>
                  {!adopter.has_badge && (
                    <button 
                      className="action-button award"
                      onClick={() => handleAwardBadge(adopter)}
                    >
                      <i className="fas fa-award"></i>
                    </button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      
      {showBadgeModal && (
        <div className="modal-overlay">
          <div className="modal">
            <div className="modal-header">
              <h3>Award Badge to {selectedAdopter?.name}</h3>
              <button className="close-modal" onClick={closeModal}>×</button>
            </div>
            <div className="modal-body">
              <p>Select a badge to award to this early adopter:</p>
              
              <div className="badges-grid">
                {badges.map(badge => (
                  <div 
                    key={badge.id}
                    className={`badge-card ${selectedBadge?.id === badge.id ? 'selected' : ''}`}
                    onClick={() => setSelectedBadge(badge)}
                  >
                    <div className="badge-icon">{badge.icon}</div>
                    <h4>{badge.name}</h4>
                    <p>{badge.description}</p>
                  </div>
                ))}
              </div>
            </div>
            <div className="modal-footer">
              <button className="cancel-button" onClick={closeModal}>Cancel</button>
              <button 
                className="confirm-button"
                disabled={!selectedBadge}
                onClick={handleConfirmBadge}
              >
                Award Badge
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default EarlyAdoptersManager;
