// frontend/src/components/social/SocialPostScheduler.jsx
import React, { useState, useEffect } from 'react';
import apiService from '../../services/api';

/**
 * Componente para programar publicaciones en redes sociales
 * Permite seleccionar múltiples plataformas y programar para una fecha específica
 */
const SocialPostScheduler = () => {
  const [post, setPost] = useState({
    content: '',
    platforms: [],
    scheduledAt: '',
  });
  
  const [connectedPlatforms, setConnectedPlatforms] = useState([]);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [message, setMessage] = useState(null);
  const [scheduledPosts, setScheduledPosts] = useState([]);
  
  // Todas las plataformas soportadas
  const platforms = [
    { id: 'instagram', name: 'Instagram', icon: 'instagram' },
    { id: 'github', name: 'GitHub', icon: 'github' },
    { id: 'reddit', name: 'Reddit', icon: 'reddit' },
    { id: 'stackoverflow', name: 'Stack Overflow', icon: 'stack-overflow' },
    { id: 'telegram', name: 'Telegram', icon: 'telegram' },
    { id: 'threads', name: 'Threads', icon: 'at' },
    { id: 'tiktok', name: 'TikTok', icon: 'video' },
    { id: 'csdn', name: 'CSDN', icon: 'code' },
  ];
  
  // Cargar plataformas conectadas y posts programados
  useEffect(() => {
    const loadData = async () => {
      try {
        // Cargar cuentas conectadas
        const accounts = await apiService.social.getAccounts();
        setConnectedPlatforms(accounts.map(account => account.platform));
        
        // Cargar posts programados
        const posts = await apiService.social.getScheduledPosts();
        setScheduledPosts(posts);
      } catch (error) {
        console.error('Error cargando datos:', error);
        setMessage({
          type: 'error',
          text: 'Error al cargar los datos de redes sociales',
        });
      }
    };
    
    loadData();
  }, []);
  
  // Manejar cambios en el contenido
  const handleContentChange = (e) => {
    setPost({
      ...post,
      content: e.target.value,
    });
  };
  
  // Manejar cambios en la fecha programada
  const handleDateChange = (e) => {
    setPost({
      ...post,
      scheduledAt: e.target.value,
    });
  };
  
  // Manejar selección de plataformas
  const handlePlatformToggle = (platformId) => {
    const platforms = [...post.platforms];
    
    if (platforms.includes(platformId)) {
      // Si ya está seleccionada, quitarla
      setPost({
        ...post,
        platforms: platforms.filter(p => p !== platformId),
      });
    } else {
      // Si no está seleccionada, añadirla
      setPost({
        ...post,
        platforms: [...platforms, platformId],
      });
    }
  };
  
  // Validar el formulario
  const validateForm = () => {
    if (!post.content.trim()) {
      setMessage({
        type: 'error',
        text: 'Por favor, escribe el contenido de la publicación',
      });
      return false;
    }
    
    if (post.platforms.length === 0) {
      setMessage({
        type: 'error',
        text: 'Por favor, selecciona al menos una plataforma',
      });
      return false;
    }
    
    if (!post.scheduledAt) {
      setMessage({
        type: 'error',
        text: 'Por favor, selecciona una fecha y hora para programar la publicación',
      });
      return false;
    }
    
    // Verificar si la fecha es futura
    const scheduledDate = new Date(post.scheduledAt);
    const now = new Date();
    
    if (scheduledDate <= now) {
      setMessage({
        type: 'error',
        text: 'La fecha de programación debe ser en el futuro',
      });
      return false;
    }
    
    return true;
  };
  
  // Programar la publicación
  const schedulePost = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }
    
    setIsSubmitting(true);
    setMessage(null);
    
    try {
      // Para cada plataforma seleccionada, crear una publicación programada
      const results = await Promise.all(
        post.platforms.map(async (platformId) => {
          // Encontrar la cuenta correspondiente a la plataforma
          const accounts = await apiService.social.getAccounts(platformId);
          
          if (!accounts || accounts.length === 0) {
            throw new Error(`No hay cuentas conectadas para ${platformId}`);
          }
          
          const account = accounts[0];
          
          // Crear la publicación programada
          return apiService.social.createPost({
            account_id: account.id,
            content: post.content,
            scheduled_at: post.scheduledAt,
          });
        })
      );
      
      // Actualizar lista de publicaciones programadas
      setScheduledPosts([...results, ...scheduledPosts]);
      
      // Mostrar mensaje de éxito
      setMessage({
        type: 'success',
        text: 'Publicación programada correctamente',
      });
      
      // Reiniciar formulario
      setPost({
        content: '',
        platforms: [],
        scheduledAt: '',
      });
    } catch (error) {
      console.error('Error programando publicación:', error);
      setMessage({
        type: 'error',
        text: 'Error al programar la publicación: ' + error.message,
      });
    } finally {
      setIsSubmitting(false);
    }
  };
  
  // Cancelar una publicación programada
  const cancelScheduledPost = async (postId) => {
    if (!window.confirm('¿Estás seguro de que deseas cancelar esta publicación programada?')) {
      return;
    }
    
    try {
      await apiService.social.updatePostStatus(postId, 'cancelled');
      
      // Actualizar la lista de publicaciones programadas
      setScheduledPosts(scheduledPosts.filter(post => post.id !== postId));
      
      setMessage({
        type: 'success',
        text: 'Publicación cancelada correctamente',
      });
    } catch (error) {
      console.error('Error cancelando publicación:', error);
      setMessage({
        type: 'error',
        text: 'Error al cancelar la publicación',
      });
    }
  };
  
  // Función para formatear una fecha
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleString();
  };
  
  // Obtener el nombre de una plataforma por su ID
  const getPlatformName = (platformId) => {
    const platform = platforms.find(p => p.id === platformId);
    return platform ? platform.name : platformId;
  };
  
  return (
    <div className="social-post-scheduler">
      <div className="scheduler-header">
        <h2>Programar Publicación</h2>
      </div>
      
      {message && (
        <div className={`message ${message.type}`}>
          {message.text}
        </div>
      )}
      
      <form onSubmit={schedulePost} className="scheduler-form">
        <div className="form-group">
          <label htmlFor="content">Contenido de la publicación:</label>
          <textarea
            id="content"
            name="content"
            value={post.content}
            onChange={handleContentChange}
            placeholder="¿Qué te gustaría compartir?"
            rows={5}
            required
          ></textarea>
          <div className="character-count">
            {post.content.length} caracteres
          </div>
        </div>
        
        <div className="form-group">
          <label>Plataformas:</label>
          <div className="platforms-grid">
            {platforms.map(platform => {
              const isConnected = connectedPlatforms.includes(platform.id);
              const isSelected = post.platforms.includes(platform.id);
              
              return (
                <div 
                  key={platform.id}
                  className={`platform-item ${isConnected ? 'connected' : 'disconnected'} ${isSelected ? 'selected' : ''}`}
                  onClick={() => isConnected && handlePlatformToggle(platform.id)}
                >
                  <div className="platform-icon">
                    <i className={`fab fa-${platform.icon}`}></i>
                  </div>
                  <span className="platform-name">{platform.name}</span>
                  
                  {!isConnected && (
                    <div className="platform-status">
                      No conectado
                    </div>
                  )}
                </div>
              );
            })}
          </div>
          
          {post.platforms.length === 0 && (
            <div className="platforms-note">
              <i className="fas fa-info-circle"></i>
              <span>Selecciona al menos una plataforma</span>
            </div>
          )}
        </div>
        
        <div className="form-group">
          <label htmlFor="scheduledAt">Programar para:</label>
          <input
            type="datetime-local"
            id="scheduledAt"
            name="scheduledAt"
            value={post.scheduledAt}
            onChange={handleDateChange}
            min={new Date().toISOString().slice(0, 16)}
            required
          />
        </div>
        
        <button 
          type="submit" 
          className="schedule-button"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Programando...' : 'Programar Publicación'}
        </button>
      </form>
      
      <div className="scheduled-posts">
        <h3>Publicaciones Programadas</h3>
        
        {scheduledPosts.length === 0 ? (
          <div className="no-posts">
            No hay publicaciones programadas
          </div>
        ) : (
          <div className="posts-list">
            {scheduledPosts.map(post => (
              <div key={post.id} className="post-item">
                <div className="post-content">
                  {post.content}
                </div>
                
                <div className="post-meta">
                  <div className="post-platform">
                    <i className={`fab fa-${platforms.find(p => p.id === post.account.platform)?.icon || 'globe'}`}></i>
                    <span>{getPlatformName(post.account.platform)}</span>
                  </div>
                  
                  <div className="post-scheduled-at">
                    <i className="far fa-clock"></i>
                    <span>{formatDate(post.scheduled_at)}</span>
                  </div>
                  
                  <div className="post-status">
                    <span className={`status-badge status-${post.status}`}>
                      {post.status === 'scheduled' ? 'Programado' : 
                       post.status === 'published' ? 'Publicado' : 
                       post.status === 'failed' ? 'Fallido' : 
                       'Cancelado'}
                    </span>
                  </div>
                </div>
                
                <div className="post-actions">
                  {post.status === 'scheduled' && (
                    <button 
                      className="cancel-button"
                      onClick={() => cancelScheduledPost(post.id)}
                    >
                      <i className="fas fa-times"></i>
                      <span>Cancelar</span>
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default SocialPostScheduler;
