// frontend/src/components/admin/AdminLayout.jsx
import React, { useContext } from 'react';
import { Link, Outlet, useLocation, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../contexts/AuthContext';
import '../../assets/css/main.css';

const AdminLayout = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { user, logout } = useContext(AuthContext);
  
  // Función para verificar si un enlace está activo
  const isActive = (path) => {
    return location.pathname === path || location.pathname.startsWith(`${path}/`);
  };
  
  // Manejar logout
  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  return (
    <div className="admin-layout">
      <aside className="admin-sidebar">
        <div className="admin-logo">
          <img src="/static/img/logo.png" alt="Adsum Blockchain" height="40" />
          <h1>Admin Panel</h1>
        </div>
        
        <nav className="admin-nav">
          <ul>
            <li>
              <Link to="/admin" className={isActive('/admin') && !isActive('/admin/content') && !isActive('/admin/early-adopters') && !isActive('/admin/social') ? 'active' : ''}>
                <i className="fas fa-tachometer-alt"></i> Dashboard
              </Link>
            </li>
            <li>
              <Link to="/admin/content" className={isActive('/admin/content') ? 'active' : ''}>
                <i className="fas fa-file-alt"></i> Contenido
              </Link>
            </li>
            <li>
              <Link to="/admin/early-adopters" className={isActive('/admin/early-adopters') ? 'active' : ''}>
                <i className="fas fa-users"></i> Early Adopters
              </Link>
            </li>
            <li>
              <Link to="/admin/social" className={isActive('/admin/social') ? 'active' : ''}>
                <i className="fas fa-share-alt"></i> Redes Sociales
              </Link>
            </li>
            <li>
              <Link to="/admin/settings" className={isActive('/admin/settings') ? 'active' : ''}>
                <i className="fas fa-cog"></i> Configuración
              </Link>
            </li>
          </ul>
        </nav>
        
        <div className="sidebar-footer">
          <a href="/" target="_blank" className="view-site-link">
            <i className="fas fa-external-link-alt"></i>
            <span>Ver sitio</span>
          </a>
        </div>
      </aside>
      
      <main className="admin-main">
        <header className="admin-header">
          <div className="admin-search">
            <input type="text" placeholder="Buscar..." />
            <button><i className="fas fa-search"></i></button>
          </div>
          
          <div className="admin-user">
            <div className="notifications">
              <button className="notification-button">
                <i className="fas fa-bell"></i>
                <span className="notification-badge">3</span>
              </button>
            </div>
            
            <div className="user-dropdown">
              <button className="user-button">
                <span>{user?.name || 'Admin'}</span>
                <img src="/static/img/admin-avatar.png" alt="Admin" className="admin-avatar" />
                <i className="fas fa-chevron-down"></i>
              </button>
              
              <div className="dropdown-menu">
                <a href="#" className="dropdown-item">
                  <i className="fas fa-user"></i>
                  <span>Mi perfil</span>
                </a>
                <a href="#" className="dropdown-item">
                  <i className="fas fa-cog"></i>
                  <span>Configuración</span>
                </a>
                <div className="dropdown-divider"></div>
                <button className="dropdown-item logout" onClick={handleLogout}>
                  <i className="fas fa-sign-out-alt"></i>
                  <span>Cerrar sesión</span>
                </button>
              </div>
            </div>
          </div>
        </header>
        
        <div className="admin-breadcrumb">
          <ul className="breadcrumb">
            <li><Link to="/admin">Dashboard</Link></li>
            {isActive('/admin/content') && <li>Gestión de Contenido</li>}
            {isActive('/admin/early-adopters') && <li>Early Adopters</li>}
            {isActive('/admin/social') && <li>Redes Sociales</li>}
            {isActive('/admin/settings') && <li>Configuración</li>}
          </ul>
        </div>
        
        <div className="admin-content">
          <Outlet />
        </div>
        
        <footer className="admin-footer">
          <p>&copy; 2025 Adsum-Blockchain. Todos los derechos reservados.</p>
          <p>Versión 1.0.0</p>
        </footer>
      </main>
    </div>
  );
};

export default AdminLayout;
