// frontend/src/components/EarlyAdopterForm.jsx
import React, { useState, useEffect } from 'react';
import apiService from '../services/api';

/**
 * Formulario para registro de early adopters
 * Se puede usar tanto en el landing page como en páginas específicas
 */
const EarlyAdopterForm = ({ preselectedSegment = null }) => {
  const [formData, setFormData] = useState({
    email: '',
    name: '',
    company: '',
    segment: preselectedSegment || 'developer', // 'developer', 'business', 'academic', 'ngo_gov'
    interests: []
  });
  
  const [availableInterests, setAvailableInterests] = useState([
    { id: 'identity', name: 'Identidad Descentralizada', description: 'Soluciones de identidad digital soberana' },
    { id: 'transparency', name: 'Transparencia', description: 'Soluciones de transparencia y rendición de cuentas' },
    { id: 'industrial', name: 'Aplicaciones Industriales', description: 'Trazabilidad y certificación en entornos industriales' }
  ]);
  
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [message, setMessage] = useState(null);
  const [errors, setErrors] = useState({});
  const [showSuccess, setShowSuccess] = useState(false);
  const [badges, setBadges] = useState([]);
  
  // Segmentos disponibles
  const segments = [
    { id: 'developer', name: 'Desarrollador', icon: 'code' },
    { id: 'business', name: 'Empresa', icon: 'building' },
    { id: 'academic', name: 'Académico', icon: 'graduation-cap' },
    { id: 'ngo_gov', name: 'ONG/Gobierno', icon: 'landmark' }
  ];
  
  // Actualizar segmento preseleccionado si cambia
  useEffect(() => {
    if (preselectedSegment) {
      setFormData(prev => ({
        ...prev,
        segment: preselectedSegment
      }));
    }
  }, [preselectedSegment]);
  
  // Cargar intereses disponibles desde la API
  useEffect(() => {
    const fetchInterests = async () => {
      try {
        // En una implementación real, cargaríamos los intereses desde la API
        // const interests = await apiService.interests.getAll();
        // setAvailableInterests(interests);
      } catch (error) {
        console.error('Error fetching interests:', error);
      }
    };
    
    fetchInterests();
  }, []);
  
  // Manejar cambios en los campos del formulario
  const handleChange = (e) => {
    const { name, value } = e.target;
    
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Limpiar errores al editar un campo
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: null
      }));
    }
  };
  
  // Manejar cambios en los checkboxes de intereses
  const handleCheckboxChange = (e) => {
    const { value, checked } = e.target;
    
    if (checked) {
      // Añadir interés a la lista
      setFormData(prev => ({
        ...prev,
        interests: [...prev.interests, value]
      }));
    } else {
      // Quitar interés de la lista
      setFormData(prev => ({
        ...prev,
        interests: prev.interests.filter(interest => interest !== value)
      }));
    }
  };
  
  // Validar el formulario
  const validateForm = () => {
    const newErrors = {};
    
    // Validar email
    if (!formData.email || !formData.email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) {
      newErrors.email = 'Por favor, introduce un email válido';
    }
    
    // Validar nombre
    if (!formData.name || formData.name.trim() === '') {
      newErrors.name = 'El nombre es obligatorio';
    }
    
    // Validar segmento
    if (!formData.segment) {
      newErrors.segment = 'Por favor, selecciona un segmento';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };
  
  // Enviar formulario
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Validar formulario
    if (!validateForm()) {
      return;
    }
    
    setIsSubmitting(true);
    setMessage(null);
    
    try {
      // Enviar datos al backend
      const response = await apiService.earlyAdopters.register(formData);
      
      // Mostrar mensaje de éxito
      setMessage({
        type: 'success',
        text: '¡Gracias por unirte al programa de early adopters! Recibirás un email con más información.'
      });
      
      // Mostrar pantalla de éxito
      setShowSuccess(true);
      
      // Simular asignación de insignia (en una implementación real, esto vendría del backend)
      setBadges([
        {
          id: 'pioneer',
          name: 'Pioneer',
          description: 'Uno de los primeros en unirse a Adsum-Blockchain',
          icon: '🚀'
        }
      ]);
      
      // Resetear formulario
      setFormData({
        email: '',
        name: '',
        company: '',
        segment: preselectedSegment || 'developer',
        interests: []
      });
    } catch (error) {
      console.error('Error registering early adopter:', error);
      
      // Mostrar mensaje de error
      setMessage({
        type: 'error',
        text: error.message || 'Ha ocurrido un error al registrarte. Por favor, inténtalo de nuevo.'
      });
    } finally {
      setIsSubmitting(false);
    }
  };
  
  // Si se ha completado el registro con éxito, mostrar pantalla de confirmación
  if (showSuccess) {
    return (
      <div className="early-adopter-success">
        <div className="success-icon">
          <i className="fas fa-check-circle"></i>
        </div>
        
        <h2>¡Bienvenido al Programa Early Adopter!</h2>
        
        <p className="success-message">
          Gracias por unirte a nuestra comunidad. Hemos enviado un email a <strong>{formData.email}</strong> con información sobre los siguientes pasos.
        </p>
        
        {badges.length > 0 && (
          <div className="badges-container">
            <h3>Has obtenido tu primera insignia:</h3>
            
            <div className="badges-list">
              {badges.map(badge => (
                <div key={badge.id} className="badge-item">
                  <div className="badge-icon">{badge.icon}</div>
                  <div className="badge-info">
                    <h4>{badge.name}</h4>
                    <p>{badge.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
        
        <div className="next-steps">
          <h3>Próximos pasos:</h3>
          <ol>
            <li>Revisa tu bandeja de entrada para confirmar tu registro</li>
            <li>Completa tu perfil con información adicional</li>
            <li>Explora los recursos exclusivos para early adopters</li>
            <li>Únete a nuestra comunidad en redes sociales</li>
          </ol>
        </div>
        
        <div className="social-links">
          <a href="#" className="social-link">
            <i className="fab fa-github"></i>
            <span>GitHub</span>
          </a>
          <a href="#" className="social-link">
            <i className="fab fa-telegram"></i>
            <span>Telegram</span>
          </a>
          <a href="#" className="social-link">
            <i className="fab fa-discord"></i>
            <span>Discord</span>
          </a>
        </div>
        
        <button
          className="new-registration-button"
          onClick={() => setShowSuccess(false)}
        >
          Registrar a otra persona
        </button>
      </div>
    );
  }
  
  // Renderizar formulario normal
  return (
    <div className="early-adopter-form">
      <h2>Únete como Early Adopter</h2>
      
      {message && (
        <div className={`message ${message.type}`}>
          <i className={`fas fa-${message.type === 'success' ? 'check' : 'exclamation'}-circle`}></i>
          <span>{message.text}</span>
        </div>
      )}
      
      <form onSubmit={handleSubmit}>
        <div className={`form-group ${errors.email ? 'has-error' : ''}`}>
          <label htmlFor="email">Email*</label>
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
            placeholder="tu@email.com"
          />
          {errors.email && <div className="error-message">{errors.email}</div>}
        </div>
        
        <div className={`form-group ${errors.name ? 'has-error' : ''}`}>
          <label htmlFor="name">Nombre*</label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
            placeholder="Tu nombre completo"
          />
          {errors.name && <div className="error-message">{errors.name}</div>}
        </div>
        
        <div className="form-group">
          <label htmlFor="company">Empresa/Organización</label>
          <input
            type="text"
            id="company"
            name="company"
            value={formData.company}
            onChange={handleChange}
            placeholder="Nombre de tu empresa u organización"
          />
        </div>
        
        <div className={`form-group ${errors.segment ? 'has-error' : ''}`}>
          <label>Soy un:*</label>
          
          <div className="segment-buttons">
            {segments.map(segment => (
              <div
                key={segment.id}
                className={`segment-button ${formData.segment === segment.id ? 'selected' : ''}`}
                onClick={() => handleChange({ target: { name: 'segment', value: segment.id } })}
              >
                <div className="segment-icon">
                  <i className={`fas fa-${segment.icon}`}></i>
                </div>
                <span className="segment-name">{segment.name}</span>
              </div>
            ))}
          </div>
          
          {errors.segment && <div className="error-message">{errors.segment}</div>}
        </div>
        
        <div className="form-group">
          <label>Me interesa:</label>
          <div className="checkbox-group">
            {availableInterests.map(interest => (
              <label key={interest.id} className="interest-checkbox">
                <input
                  type="checkbox"
                  name="interests"
                  value={interest.id}
                  checked={formData.interests.includes(interest.id)}
                  onChange={handleCheckboxChange}
                />
                <div className="checkbox-content">
                  <span className="interest-name">{interest.name}</span>
                  <span className="interest-description">{interest.description}</span>
                </div>
              </label>
            ))}
          </div>
        </div>
        
        <div className="form-group consent-group">
          <label className="consent-checkbox">
            <input
              type="checkbox"
              name="consent"
              required
            />
            <span>Acepto recibir comunicaciones relacionadas con el programa de early adopters*</span>
          </label>
        </div>
        
        <button 
          type="submit" 
          className="submit-button"
          disabled={isSubmitting}
        >
          {isSubmitting ? (
            <>
              <div className="button-spinner"></div>
              <span>Enviando...</span>
            </>
          ) : (
            <>
              <i className="fas fa-rocket"></i>
              <span>Unirme al Programa</span>
            </>
          )}
        </button>
        
        <div className="form-note">
          * Campos obligatorios. Consulta nuestra <a href="#">Política de Privacidad</a>.
        </div>
      </form>
    </div>
  );
};

export default EarlyAdopterForm;
