// frontend/src/contexts/LanguageContext.js
import React, { createContext, useState, useEffect } from 'react';

export const LanguageContext = createContext();

export const LanguageProvider = ({ children }) => {
  const [language, setLanguage] = useState('en');
  const [translations, setTranslations] = useState({});
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Traducciones de fallback en caso de que no se puedan cargar
  const fallbackTranslations = {
    common: {
      loading: 'Loading...',
      save: 'Save',
      cancel: 'Cancel',
      edit: 'Edit',
      delete: 'Delete'
    },
    navigation: {
      home: 'Home',
      about: 'About',
      contact: 'Contact'
    },
    hero: {
      title: 'Blockchain without crypto, only solutions',
      subtitle: 'The blockchain platform that democratizes technology for real-world applications'
    }
  };

  useEffect(() => {
    loadTranslations(language);
  }, [language]);

  const loadTranslations = async (lang) => {
    setLoading(true);
    setError(null);

    try {
      // CAMBIO IMPORTANTE: usar ruta relativa para que funcione en /nueva/
      const response = await fetch(`./lang/${lang}.json`);

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const text = await response.text();

      // Verificar si el archivo está vacío
      if (!text.trim()) {
        throw new Error('Empty translation file');
      }

      // Intentar parsear el JSON
      const data = JSON.parse(text);

      // Verificar si el JSON tiene contenido
      if (!data || Object.keys(data).length === 0) {
        throw new Error('Empty translation data');
      }

      setTranslations(data);

    } catch (error) {
      console.warn(`Failed to load translations for ${lang}:`, error.message);

      // Intentar cargar desde la API como fallback
      try {
        const apiResponse = await fetch(`/api/languages/${lang}`);
        if (apiResponse.ok) {
          const apiData = await apiResponse.json();
          setTranslations(apiData);
        } else {
          throw new Error('API also failed');
        }
      } catch (apiError) {
        console.warn(`API fallback also failed for ${lang}:`, apiError.message);

        // Si todo falla, usar traducciones de fallback
        if (lang === 'en') {
          // Para inglés, usar las traducciones de fallback
          setTranslations(fallbackTranslations);
        } else {
          // Para otros idiomas, intentar cargar inglés como último recurso
          console.warn(`Loading English as fallback for ${lang}`);
          try {
            // CAMBIO IMPORTANTE: usar ruta relativa también aquí
            const enResponse = await fetch('./lang/en.json');
            if (enResponse.ok) {
              const enText = await enResponse.text();
              if (enText.trim()) {
                const enData = JSON.parse(enText);
                setTranslations(enData);
              } else {
                setTranslations(fallbackTranslations);
              }
            } else {
              setTranslations(fallbackTranslations);
            }
          } catch (enError) {
            console.error('Even English fallback failed:', enError);
            setTranslations(fallbackTranslations);
          }
        }

        setError(`Failed to load translations for ${lang}`);
      }
    } finally {
      setLoading(false);
    }
  };

  // Función helper para obtener una traducción con fallback
  const t = (key, fallback = key) => {
    const keys = key.split('.');
    let value = translations;

    for (const k of keys) {
      if (value && typeof value === 'object' && k in value) {
        value = value[k];
      } else {
        return fallback;
      }
    }

    return typeof value === 'string' ? value : fallback;
  };

  return (
    <LanguageContext.Provider value={{
      language,
      setLanguage,
      translations,
      loading,
      error,
      t
    }}>
      {children}
    </LanguageContext.Provider>
  );
};
