// frontend/src/services/analyticsService.js
/**
 * Servicio para tracear eventos y páginas vistas en el sistema
 * Integra con Google Analytics y también captura eventos personalizados
 */

// Configuración
const ENABLE_GOOGLE_ANALYTICS = process.env.REACT_APP_ENABLE_GA === 'true';
const ENABLE_CUSTOM_ANALYTICS = process.env.REACT_APP_ENABLE_CUSTOM_ANALYTICS === 'true';
const GA_TRACKING_ID = process.env.REACT_APP_GA_TRACKING_ID || '';
const API_URL = process.env.REACT_APP_API_URL || '';

// Cargar Google Analytics si está habilitado
if (ENABLE_GOOGLE_ANALYTICS && GA_TRACKING_ID) {
  // Añadir el script de Google Analytics
  const script = document.createElement('script');
  script.async = true;
  script.src = `https://www.googletagmanager.com/gtag/js?id=${GA_TRACKING_ID}`;
  document.head.appendChild(script);
  
  // Configurar Google Analytics
  window.dataLayer = window.dataLayer || [];
  window.gtag = function() {
    window.dataLayer.push(arguments);
  };
  window.gtag('js', new Date());
  window.gtag('config', GA_TRACKING_ID);
}

/**
 * Enviar un evento a los sistemas de analytics
 * @param {string} category - Categoría del evento
 * @param {string} action - Acción realizada
 * @param {string} label - Etiqueta descriptiva (opcional)
 * @param {number} value - Valor numérico (opcional)
 */
const trackEvent = (category, action, label = null, value = null) => {
  // Enviar a Google Analytics
  if (ENABLE_GOOGLE_ANALYTICS && window.gtag) {
    window.gtag('event', action, {
      'event_category': category,
      'event_label': label,
      'value': value
    });
  }
  
  // Enviar a nuestro sistema personalizado
  if (ENABLE_CUSTOM_ANALYTICS) {
    const eventData = {
      category,
      action,
      label,
      value,
      timestamp: new Date().toISOString(),
      path: window.location.pathname,
      user_agent: navigator.userAgent
    };
    
    // En una implementación real, enviaríamos esto a nuestro backend
    fetch(`${API_URL}/analytics/events`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(eventData),
      // Usamos keepalive para asegurar que la petición se complete incluso si la página se cierra
      keepalive: true
    }).catch(err => {
      console.error('Error sending analytics event:', err);
    });
  }
  
  // Log en consola en desarrollo
  if (process.env.NODE_ENV === 'development') {
    console.log(`[Analytics] ${category} - ${action}${label ? ` - ${label}` : ''}${value ? ` - ${value}` : ''}`);
  }
};

/**
 * Registrar una vista de página
 * @param {string} path - Ruta de la página (opcional, por defecto la ruta actual)
 * @param {string} title - Título de la página (opcional, por defecto el título del documento)
 */
const trackPageView = (path = null, title = null) => {
  const currentPath = path || window.location.pathname + window.location.search;
  const pageTitle = title || document.title;
  
  // Enviar a Google Analytics
  if (ENABLE_GOOGLE_ANALYTICS && window.gtag) {
    window.gtag('event', 'page_view', {
      page_path: currentPath,
      page_title: pageTitle,
      page_location: window.location.href
    });
  }
  
  // Enviar a nuestro sistema personalizado
  if (ENABLE_CUSTOM_ANALYTICS) {
    const pageViewData = {
      path: currentPath,
      title: pageTitle,
      referrer: document.referrer,
      timestamp: new Date().toISOString(),
      user_agent: navigator.userAgent,
      screen_resolution: `${window.screen.width}x${window.screen.height}`,
      viewport_size: `${window.innerWidth}x${window.innerHeight}`
    };
    
    // En una implementación real, enviaríamos esto a nuestro backend
    fetch(`${API_URL}/analytics/pageviews`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(pageViewData),
      keepalive: true
    }).catch(err => {
      console.error('Error sending pageview:', err);
    });
  }
  
  // Log en consola en desarrollo
  if (process.env.NODE_ENV === 'development') {
    console.log(`[Analytics] PageView - ${currentPath}`);
  }
};

/**
 * Registrar un evento de usuario
 * @param {string} eventName - Nombre del evento
 * @param {Object} properties - Propiedades adicionales del evento
 */
const trackUserEvent = (eventName, properties = {}) => {
  trackEvent('User', eventName, null, null);
  
  // En una implementación real, podríamos querer enviar las propiedades específicas
  // a una herramienta de analytics más avanzada
};

/**
 * Registrar una conversión (inscripción, formulario completado, etc.)
 * @param {string} type - Tipo de conversión
 * @param {Object} data - Datos asociados a la conversión
 */
const trackConversion = (type, data = {}) => {
  // Enviar a Google Analytics
  if (ENABLE_GOOGLE_ANALYTICS && window.gtag) {
    window.gtag('event', 'conversion', {
      'send_to': `${GA_TRACKING_ID}/${type}`,
      ...data
    });
  }
  
  // Enviar a nuestro sistema personalizado
  if (ENABLE_CUSTOM_ANALYTICS) {
    const conversionData = {
      type,
      data,
      timestamp: new Date().toISOString(),
      path: window.location.pathname
    };
    
    // En una implementación real, enviaríamos esto a nuestro backend
    fetch(`${API_URL}/analytics/conversions`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(conversionData),
      keepalive: true
    }).catch(err => {
      console.error('Error sending conversion:', err);
    });
  }
  
  // Log en consola en desarrollo
  if (process.env.NODE_ENV === 'development') {
    console.log(`[Analytics] Conversion - ${type}`, data);
  }
};

// Categorías comunes para eventos
const CATEGORIES = {
  NAVIGATION: 'Navigation',
  CONTENT: 'Content',
  FORMS: 'Forms',
  USER: 'User',
  SOCIAL: 'Social',
  ADMIN: 'Admin'
};

// Acciones comunes para eventos
const ACTIONS = {
  CLICK: 'Click',
  VIEW: 'View',
  SUBMIT: 'Submit',
  EDIT: 'Edit',
  CREATE: 'Create',
  DELETE: 'Delete',
  LOGIN: 'Login',
  LOGOUT: 'Logout',
  REGISTER: 'Register',
  SHARE: 'Share'
};

// Tipos de conversión
const CONVERSION_TYPES = {
  EARLY_ADOPTER: 'early_adopter_signup',
  CONTACT_FORM: 'contact_form_submit',
  PATREON_SIGNUP: 'patreon_signup',
  NEWSLETTER: 'newsletter_signup'
};

// Exportar todas las funciones y constantes
const analyticsService = {
  trackEvent,
  trackPageView,
  trackUserEvent,
  trackConversion,
  CATEGORIES,
  ACTIONS,
  CONVERSION_TYPES
};

export default analyticsService;
