// Componente para SEO
// frontend/src/components/SEOHead.jsx
import React from 'react';
import { Helmet } from 'react-helmet';

const SEOHead = ({ title, description, keywords, language, canonical }) => {
  return (
    <Helmet>
      <html lang={language || 'en'} />
      <title>{title}</title>
      <meta name="description" content={description} />
      <meta name="keywords" content={keywords} />
      <link rel="canonical" href={canonical} />
      
      {/* Google */}
      <meta name="robots" content="index, follow" />
      
      {/* Baidu */}
      <meta name="baidu-site-verification" content="your-baidu-code" />
      
      {/* Open Graph */}
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={canonical} />
      <meta property="og:image" content="/static/img/og-image.jpg" />
      
      {/* Hreflang para multidioma */}
      <link rel="alternate" hreflang="en" href={`https://adsum-blockchain.com/?lang=en`} />
      <link rel="alternate" hreflang="es" href={`https://adsum-blockchain.com/?lang=es`} />
      <link rel="alternate" hreflang="fr" href={`https://adsum-blockchain.com/?lang=fr`} />
      <link rel="alternate" hreflang="de" href={`https://adsum-blockchain.com/?lang=de`} />
      <link rel="alternate" hreflang="it" href={`https://adsum-blockchain.com/?lang=it`} />
      <link rel="alternate" hreflang="pt" href={`https://adsum-blockchain.com/?lang=pt`} />
      <link rel="alternate" hreflang="ru" href={`https://adsum-blockchain.com/?lang=ru`} />
      <link rel="alternate" hreflang="zh" href={`https://adsum-blockchain.com/?lang=zh`} />
      <link rel="alternate" hreflang="x-default" href="https://adsum-blockchain.com/" />
    </Helmet>
  );
};

export default SEOHead;
