// frontend/src/pages/admin/ContentManagement.jsx
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import ContentEditor from '../../components/ContentEditor';

const ContentManagement = () => {
  const [contentList, setContentList] = useState([]);
  const [selectedContent, setSelectedContent] = useState(null);
  const [selectedLanguage, setSelectedLanguage] = useState('en');
  const [isEditing, setIsEditing] = useState(false);
  const [availableLanguages] = useState(['en', 'es', 'fr', 'de', 'it', 'pt', 'ru', 'zh']);
  const [searchQuery, setSearchQuery] = useState('');
  const [contentStructure] = useState([
    { key: 'home-hero', title: 'Home Hero Section' },
    { key: 'home-problem', title: 'Problem & Solution Section' },
    { key: 'home-cases', title: 'Use Cases Section' },
    { key: 'home-why', title: 'Why Adsum Section' },
    { key: 'home-community', title: 'Community Section' },
    { key: 'about-mission', title: 'About - Mission' },
    { key: 'about-team', title: 'About - Team' },
    { key: 'about-technology', title: 'About - Technology' },
    { key: 'use-cases-identity', title: 'Use Case - Identity' },
    { key: 'use-cases-transparency', title: 'Use Case - Transparency' },
    { key: 'use-cases-industrial', title: 'Use Case - Industrial' },
  ]);
  
  useEffect(() => {
    // En una implementación real, cargaríamos los contenidos desde la API
    // Por ahora, generamos datos de ejemplo
    const generateContentList = () => {
      const result = [];
      
      // Generar algunos contenidos de ejemplo
      contentStructure.forEach(structure => {
        const languages = [];
        
        // Añadir algunos idiomas aleatoriamente
        availableLanguages.forEach(lang => {
          if (lang === 'en' || Math.random() > 0.3) {
            languages.push({
              language: lang,
              updated_at: new Date(Date.now() - Math.floor(Math.random() * 30) * 24 * 60 * 60 * 1000).toISOString(),
              version: Math.floor(Math.random() * 5) + 1
            });
          }
        });
        
        result.push({
          key: structure.key,
          title: structure.title,
          languages
        });
      });
      
      setContentList(result);
    };
    
    generateContentList();
  }, [contentStructure, availableLanguages]);
  
  const handleSearch = (e) => {
    setSearchQuery(e.target.value);
  };
  
  const filteredContent = contentList.filter(content => 
    content.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
    content.key.toLowerCase().includes(searchQuery.toLowerCase())
  );
  
  const handleSelectContent = (content) => {
    setSelectedContent(content);
    setIsEditing(true);
  };
  
  const handleCreateNew = () => {
    setSelectedContent(null);
    setIsEditing(true);
  };
  
  const handleBack = () => {
    setIsEditing(false);
    setSelectedContent(null);
  };
  
  return (
    <div className="content-management">
      {!isEditing ? (
        <>
          <div className="page-header">
            <h1>Content Management</h1>
            <button className="btn btn-primary" onClick={handleCreateNew}>
              <i className="fas fa-plus"></i> Create New Content
            </button>
          </div>
          
          <div className="search-bar">
            <input 
              type="text" 
              placeholder="Search content..." 
              value={searchQuery}
              onChange={handleSearch}
            />
            <button><i className="fas fa-search"></i></button>
          </div>
          
          <div className="content-list">
            <table className="data-table">
              <thead>
                <tr>
                  <th>Content Key</th>
                  <th>Title</th>
                  <th>Languages</th>
                  <th>Last Updated</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {filteredContent.map(content => (
                  <tr key={content.key}>
                    <td>{content.key}</td>
                    <td>{content.title}</td>
                    <td>
                      <div className="language-badges">
                        {content.languages.map(lang => (
                          <span key={lang.language} className="language-badge">
                            {lang.language.toUpperCase()}
                          </span>
                        ))}
                      </div>
                    </td>
                    <td>
                      {content.languages.length > 0 
                        ? new Date(Math.max(...content.languages.map(l => new Date(l.updated_at)))).toLocaleDateString() 
                        : 'N/A'}
                    </td>
                    <td>
                      <button 
                        className="action-button edit"
                        onClick={() => handleSelectContent(content)}
                      >
                        <i className="fas fa-edit"></i> Edit
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </>
      ) : (
        <div className="content-editor-container">
          <div className="editor-header">
            <button className="back-button" onClick={handleBack}>
              <i className="fas fa-arrow-left"></i> Back to List
            </button>
            <h2>
              {selectedContent 
                ? `Editing: ${selectedContent.title}` 
                : 'Create New Content'}
            </h2>
          </div>
          
          {!selectedContent && (
            <div className="content-key-selector">
              <label htmlFor="content-key">Select Content Type:</label>
              <select id="content-key" className="form-control">
                {contentStructure.map(item => (
                  <option key={item.key} value={item.key}>
                    {item.title}
                  </option>
                ))}
              </select>
            </div>
          )}
          
          <ContentEditor 
            contentKey={selectedContent?.key} 
            initialLanguage={selectedLanguage}
          />
        </div>
      )}
    </div>
  );
};

export default ContentManagement;
