// frontend/src/contexts/AuthContext.js
import React, { createContext, useState, useEffect } from 'react';
import { authService } from '../services/api';

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  
  // Verificar autenticación al cargar el contexto
  useEffect(() => {
    const checkAuth = async () => {
      const isAuth = authService.isAuthenticated();
      setIsAuthenticated(isAuth);
      
      // Si está autenticado, obtener datos del usuario
      // En una implementación real, esto requeriría un endpoint para obtener el perfil
      if (isAuth) {
        setUser({
          name: 'Admin',
          role: 'admin',
        });
      }
      
      setLoading(false);
    };
    
    checkAuth();
  }, []);
  
  // Función para iniciar sesión
  const login = async (email, password) => {
    try {
      const data = await authService.login(email, password);
      
      setIsAuthenticated(true);
      setUser({
        name: 'Admin', // En implementación real, esto vendría del backend
        role: 'admin',
      });
      
      return { success: true };
    } catch (error) {
      return { success: false, message: error.message };
    }
  };
  
  // Función para cerrar sesión
  const logout = () => {
    authService.logout();
    setIsAuthenticated(false);
    setUser(null);
  };
  
  return (
    <AuthContext.Provider
      value={{
        isAuthenticated,
        user,
        loading,
        login,
        logout,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
