// frontend/src/pages/admin/SocialMediaManager.jsx
import React, { useState, useEffect } from 'react';
import SocialConnector from '../../components/social/SocialConnector';
import SocialPostScheduler from '../../components/social/SocialPostScheduler';
import apiService from '../../services/api';

const SocialMediaManager = () => {
  const [activeTab, setActiveTab] = useState('accounts'); // 'accounts', 'schedule', 'analytics'
  const [connectedAccounts, setConnectedAccounts] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [analytics, setAnalytics] = useState({
    totalPosts: 0,
    publishedPosts: 0,
    scheduledPosts: 0,
    failedPosts: 0,
    engagementByPlatform: {},
  });
  
  // Lista de plataformas soportadas
  const platforms = [
    { id: 'instagram', name: 'Instagram', icon: 'instagram' },
    { id: 'github', name: 'GitHub', icon: 'github' },
    { id: 'reddit', name: 'Reddit', icon: 'reddit' },
    { id: 'stackoverflow', name: 'Stack Overflow', icon: 'stack-overflow' },
    { id: 'telegram', name: 'Telegram', icon: 'telegram' },
    { id: 'threads', name: 'Threads', icon: 'at' },
    { id: 'tiktok', name: 'TikTok', icon: 'video' },
    { id: 'csdn', name: 'CSDN', icon: 'code' },
  ];
  
  // Cargar cuentas conectadas y analíticas
  useEffect(() => {
    const loadData = async () => {
      setIsLoading(true);
      
      try {
        // Cargar cuentas conectadas
        const accounts = await apiService.social.getAccounts();
        setConnectedAccounts(accounts);
        
        // En una implementación real, cargaríamos las analíticas desde la API
        // Por ahora, usamos datos simulados
        loadMockAnalytics();
      } catch (error) {
        console.error('Error loading data:', error);
      } finally {
        setIsLoading(false);
      }
    };
    
    loadData();
  }, []);
  
  // Cargar datos simulados de analíticas
  const loadMockAnalytics = () => {
    // En una implementación real, estos datos vendrían de la API
    setAnalytics({
      totalPosts: 87,
      publishedPosts: 72,
      scheduledPosts: 8,
      failedPosts: 7,
      engagementByPlatform: {
        instagram: { likes: 523, comments: 128, shares: 45 },
        github: { stars: 38, forks: 12, watches: 22 },
        reddit: { upvotes: 156, comments: 42, awards: 3 },
        telegram: { views: 318, forwards: 24 },
        tiktok: { views: 1275, likes: 342, comments: 61, shares: 28 },
      },
      topPosts: [
        {
          id: 1,
          platform: 'instagram',
          content: 'Descubre cómo utilizar blockchain para transparencia institucional #AdsumBlockchain',
          metrics: { likes: 87, comments: 23, shares: 12 },
          publishedAt: '2025-05-10T14:35:00Z',
        },
        {
          id: 2,
          platform: 'github',
          content: 'Publicamos nuestra guía de inicio rápido para desarrolladores: blockchain sin especulación.',
          metrics: { stars: 15, forks: 7 },
          publishedAt: '2025-05-12T11:20:00Z',
        },
        {
          id: 3,
          platform: 'tiktok',
          content: 'Blockchain explicada en 60 segundos: más allá de las criptomonedas #Tech #Blockchain',
          metrics: { views: 934, likes: 221, comments: 47, shares: 18 },
          publishedAt: '2025-05-15T18:45:00Z',
        },
      ],
    });
  };
  
  // Manejar cuando se conecta una nueva cuenta
  const handleAccountConnect = (platform, accountInfo) => {
    setConnectedAccounts([...connectedAccounts, accountInfo]);
  };
  
  // Manejar cuando se desconecta una cuenta
  const handleAccountDisconnect = (platform, accountId) => {
    setConnectedAccounts(connectedAccounts.filter(account => account.id !== accountId));
  };
  
  // Renderizar la pestaña activa
  const renderActiveTab = () => {
    switch (activeTab) {
      case 'accounts':
        return (
          <div className="accounts-tab">
            <h2>Cuentas Conectadas</h2>
            <p className="tab-description">
              Conecta tus redes sociales para programar publicaciones y gestionar tu presencia online.
            </p>
            
            <div className="connectors-grid">
              {platforms.map(platform => (
                <SocialConnector 
                  key={platform.id}
                  platform={platform.id}
                  onConnect={(accountInfo) => handleAccountConnect(platform.id, accountInfo)}
                  onDisconnect={(accountId) => handleAccountDisconnect(platform.id, accountId)}
                />
              ))}
            </div>
          </div>
        );
      case 'schedule':
        return (
          <div className="schedule-tab">
            <h2>Programar Publicaciones</h2>
            <p className="tab-description">
              Programa publicaciones para todas tus redes sociales desde un solo lugar.
            </p>
            
            <SocialPostScheduler />
          </div>
        );
      case 'analytics':
        return (
          <div className="analytics-tab">
            <h2>Analíticas</h2>
            <p className="tab-description">
              Analiza el rendimiento de tus publicaciones en redes sociales.
            </p>
            
            <div className="analytics-summary">
              <div className="analytics-card">
                <div className="analytics-value">{analytics.totalPosts}</div>
                <div className="analytics-label">Publicaciones Totales</div>
              </div>
              
              <div className="analytics-card">
                <div className="analytics-value">{analytics.publishedPosts}</div>
                <div className="analytics-label">Publicadas</div>
              </div>
              
              <div className="analytics-card">
                <div className="analytics-value">{analytics.scheduledPosts}</div>
                <div className="analytics-label">Programadas</div>
              </div>
              
              <div className="analytics-card">
                <div className="analytics-value">{analytics.failedPosts}</div>
                <div className="analytics-label">Fallidas</div>
              </div>
            </div>
            
            <div className="analytics-section">
              <h3>Engagement por Plataforma</h3>
              
              <div className="platform-analytics">
                {Object.entries(analytics.engagementByPlatform).map(([platform, metrics]) => {
                  const platformInfo = platforms.find(p => p.id === platform);
                  
                  return (
                    <div key={platform} className="platform-card">
                      <div className="platform-header">
                        <i className={`fab fa-${platformInfo?.icon || 'globe'}`}></i>
                        <span>{platformInfo?.name || platform}</span>
                      </div>
                      
                      <div className="metrics-grid">
                        {Object.entries(metrics).map(([metric, value]) => (
                          <div key={metric} className="metric-card">
                            <div className="metric-value">{value}</div>
                            <div className="metric-label">
                              {metric.charAt(0).toUpperCase() + metric.slice(1)}
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
            
            <div className="analytics-section">
              <h3>Publicaciones Destacadas</h3>
              
              <div className="top-posts">
                {analytics.topPosts.map(post => {
                  const platformInfo = platforms.find(p => p.id === post.platform);
                  
                  return (
                    <div key={post.id} className="top-post-card">
                      <div className="post-platform">
                        <i className={`fab fa-${platformInfo?.icon || 'globe'}`}></i>
                        <span>{platformInfo?.name || post.platform}</span>
                      </div>
                      
                      <div className="post-content">
                        {post.content}
                      </div>
                      
                      <div className="post-metrics">
                        {Object.entries(post.metrics).map(([metric, value]) => (
                          <div key={metric} className="post-metric">
                            <span className="metric-value">{value}</span>
                            <span className="metric-name">
                              {metric.charAt(0).toUpperCase() + metric.slice(1)}
                            </span>
                          </div>
                        ))}
                      </div>
                      
                      <div className="post-date">
                        Publicado: {new Date(post.publishedAt).toLocaleDateString()}
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        );
      default:
        return <div>Tab not found</div>;
    }
  };
  
  return (
    <div className="social-media-manager">
      <div className="page-header">
        <h1>Gestión de Redes Sociales</h1>
      </div>
      
      <div className="tab-navigation">
        <button 
          className={`tab-button ${activeTab === 'accounts' ? 'active' : ''}`}
          onClick={() => setActiveTab('accounts')}
        >
          <i className="fas fa-user-circle"></i>
          <span>Cuentas</span>
        </button>
        
        <button 
          className={`tab-button ${activeTab === 'schedule' ? 'active' : ''}`}
          onClick={() => setActiveTab('schedule')}
        >
          <i className="fas fa-calendar-alt"></i>
          <span>Programación</span>
        </button>
        
        <button 
          className={`tab-button ${activeTab === 'analytics' ? 'active' : ''}`}
          onClick={() => setActiveTab('analytics')}
        >
          <i className="fas fa-chart-line"></i>
          <span>Analíticas</span>
        </button>
      </div>
      
      <div className="tab-content">
        {isLoading ? (
          <div className="loading-container">
            <div className="loading-spinner"></div>
            <p>Cargando...</p>
          </div>
        ) : (
          renderActiveTab()
        )}
      </div>
    </div>
  );
};

export default SocialMediaManager;
