// frontend/src/components/ContentEditor.jsx
import React, { useState, useEffect } from 'react';
import ReactQuill from 'react-quill';
import 'quill/dist/quill.snow.css';
import apiService from '../services/api';

/**
 * Editor de contenido multicapa y multiidioma
 * Permite editar y versionar contenido en múltiples idiomas
 */
const ContentEditor = ({ contentKey, initialLanguage = 'en' }) => {
  const [content, setContent] = useState({});
  const [contentMetadata, setContentMetadata] = useState({});
  const [currentLanguage, setCurrentLanguage] = useState(initialLanguage);
  const [availableLanguages] = useState(['en', 'es', 'fr', 'de', 'it', 'pt', 'ru', 'zh']);
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [versions, setVersions] = useState({});
  const [currentVersions, setCurrentVersions] = useState({});
  const [message, setMessage] = useState(null);
  const [publishStatus, setPublishStatus] = useState({});
  const [seoData, setSeoData] = useState({});
  
  // Diccionario de nombres de idiomas
  const languageNames = {
    en: 'English',
    es: 'Español',
    fr: 'Français',
    de: 'Deutsch',
    it: 'Italiano',
    pt: 'Português',
    ru: 'Русский',
    zh: '中文',
  };
  
  // Cargar contenido existente al montar el componente
  useEffect(() => {
    if (contentKey) {
      fetchContent(contentKey);
    } else {
      // Si no hay contentKey, inicializar con contenido vacío
      initializeEmptyContent();
    }
  }, [contentKey]);
  
  // Inicializar con contenido vacío
  const initializeEmptyContent = () => {
    const emptyContent = {};
    const emptySeo = {};
    const emptyPublishStatus = {};
    
    availableLanguages.forEach(lang => {
      emptyContent[lang] = '';
      emptySeo[lang] = {
        title: '',
        description: '',
        keywords: '',
      };
      emptyPublishStatus[lang] = false;
    });
    
    setContent(emptyContent);
    setSeoData(emptySeo);
    setPublishStatus(emptyPublishStatus);
    setIsLoading(false);
  };
  
  // Cargar contenido existente desde la API
  const fetchContent = async (key) => {
    setIsLoading(true);
    
    try {
      // Cargar todas las versiones del contenido
      const allVersions = await apiService.content.getAllVersions(key);
      
      // Procesar y organizar las versiones por idioma
      const contentByLang = {};
      const versionsByLang = {};
      const currentVersionsByLang = {};
      const metadataByLang = {};
      const publishStatusByLang = {};
      const seoByLang = {};
      
      // Agrupar las versiones por idioma
      allVersions.forEach(item => {
        if (!versionsByLang[item.language]) {
          versionsByLang[item.language] = [];
        }
        
        versionsByLang[item.language].push(item);
      });
      
      // Para cada idioma, obtener la versión más reciente
      availableLanguages.forEach(lang => {
        const langVersions = versionsByLang[lang] || [];
        const latestVersion = langVersions.sort((a, b) => b.version - a.version)[0];
        
        if (latestVersion) {
          contentByLang[lang] = latestVersion.content;
          currentVersionsByLang[lang] = latestVersion.version;
          publishStatusByLang[lang] = latestVersion.published;
          
          // Extraer metadatos SEO si existen
          seoByLang[lang] = {
            title: latestVersion.seo_title || '',
            description: latestVersion.seo_description || '',
            keywords: latestVersion.seo_keywords || '',
          };
          
          // Almacenar metadatos adicionales
          metadataByLang[lang] = {
            id: latestVersion.id,
            created_at: latestVersion.created_at,
            updated_at: latestVersion.updated_at,
          };
        } else {
          // Si no hay versión para este idioma, inicializar con contenido vacío
          contentByLang[lang] = '';
          currentVersionsByLang[lang] = 0;
          publishStatusByLang[lang] = false;
          seoByLang[lang] = {
            title: '',
            description: '',
            keywords: '',
          };
          metadataByLang[lang] = null;
        }
      });
      
      setContent(contentByLang);
      setVersions(versionsByLang);
      setCurrentVersions(currentVersionsByLang);
      setContentMetadata(metadataByLang);
      setPublishStatus(publishStatusByLang);
      setSeoData(seoByLang);
    } catch (error) {
      console.error('Error fetching content:', error);
      setMessage({
        type: 'error',
        text: 'Error al cargar el contenido: ' + error.message,
      });
      
      // Inicializar con contenido vacío en caso de error
      initializeEmptyContent();
    } finally {
      setIsLoading(false);
    }
  };
  
  // Manejar cambios en el editor de contenido
  const handleContentChange = (value) => {
    setContent({
      ...content,
      [currentLanguage]: value,
    });
  };
  
  // Manejar cambios en los campos SEO
  const handleSeoChange = (field, value) => {
    setSeoData({
      ...seoData,
      [currentLanguage]: {
        ...seoData[currentLanguage],
        [field]: value,
      },
    });
  };
  
  // Guardar contenido en todos los idiomas
  const saveContent = async () => {
    setIsSaving(true);
    setMessage(null);
    
    try {
      // Crear un array de promesas para guardar cada idioma
      const savePromises = Object.entries(content).map(([lang, text]) => {
        // Solo guardar si hay contenido o metadatos SEO
        if (text || Object.values(seoData[lang]).some(v => v)) {
          return apiService.content.createOrUpdate({
            key: contentKey || `content-${Date.now()}`,
            language: lang,
            content: text,
            version: currentVersions[lang] ? currentVersions[lang] + 1 : 1,
            published: publishStatus[lang] || false,
            seo_title: seoData[lang].title,
            seo_description: seoData[lang].description,
            seo_keywords: seoData[lang].keywords,
          });
        }
        
        // Si no hay contenido, no guardar
        return Promise.resolve(null);
      });
      
      // Esperar a que se completen todas las peticiones
      const responses = await Promise.all(savePromises);
      
      // Filtrar las respuestas nulas
      const validResponses = responses.filter(r => r !== null);
      
      if (validResponses.length > 0) {
        // Actualizar el contentKey si es una creación
        if (!contentKey) {
          const newKey = validResponses[0].key;
          // En una implementación real, redirigiríamos a la URL con el nuevo contentKey
          console.log('New content key:', newKey);
        }
        
        // Actualizar estado con los nuevos datos
        fetchContent(contentKey || validResponses[0].key);
        
        setMessage({
          type: 'success',
          text: `Contenido guardado correctamente en ${validResponses.length} idioma(s)`,
        });
      } else {
        setMessage({
          type: 'warning',
          text: 'No hay cambios que guardar',
        });
      }
    } catch (error) {
      console.error('Error saving content:', error);
      setMessage({
        type: 'error',
        text: 'Error al guardar el contenido: ' + error.message,
      });
    } finally {
      setIsSaving(false);
    }
  };
  
  // Publicar contenido en el idioma actual
  const publishCurrentLanguage = async () => {
    if (!contentMetadata[currentLanguage]?.id) {
      setMessage({
        type: 'error',
        text: 'Debes guardar el contenido antes de publicarlo',
      });
      return;
    }
    
    try {
      await apiService.content.publish(contentMetadata[currentLanguage].id);
      
      // Actualizar estado local
      setPublishStatus({
        ...publishStatus,
        [currentLanguage]: true,
      });
      
      setMessage({
        type: 'success',
        text: `Contenido publicado correctamente en ${languageNames[currentLanguage]}`,
      });
    } catch (error) {
      console.error('Error publishing content:', error);
      setMessage({
        type: 'error',
        text: 'Error al publicar el contenido: ' + error.message,
      });
    }
  };
  
  // Despublicar contenido en el idioma actual
  const unpublishCurrentLanguage = async () => {
    if (!contentMetadata[currentLanguage]?.id) {
      return;
    }
    
    try {
      // En una implementación real, debería haber un endpoint específico para despublicar
      // Por ahora, simulamos la actualización del estado local
      setPublishStatus({
        ...publishStatus,
        [currentLanguage]: false,
      });
      
      setMessage({
        type: 'success',
        text: `Contenido despublicado correctamente en ${languageNames[currentLanguage]}`,
      });
    } catch (error) {
      console.error('Error unpublishing content:', error);
      setMessage({
        type: 'error',
        text: 'Error al despublicar el contenido: ' + error.message,
      });
    }
  };
  
  // Calcular el progreso de traducción
  const calculateTranslationProgress = () => {
    const totalLanguages = availableLanguages.length;
    const translatedLanguages = Object.entries(content).filter(([lang, text]) => text?.trim()).length;
    
    return Math.round((translatedLanguages / totalLanguages) * 100);
  };
  
  // Configuración del editor Quill
  const editorModules = {
    toolbar: [
      [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
      ['bold', 'italic', 'underline', 'strike'],
      [{ 'list': 'ordered'}, { 'list': 'bullet' }],
      [{ 'indent': '-1'}, { 'indent': '+1' }],
      [{ 'direction': 'rtl' }],
      [{ 'color': [] }, { 'background': [] }],
      ['link', 'image', 'video'],
      ['clean']
    ],
  };
  
  // Renderizar spinner de carga
  if (isLoading) {
    return (
      <div className="content-editor-loading">
        <div className="loading-spinner"></div>
        <p>Cargando editor de contenido...</p>
      </div>
    );
  }
  
  return (
    <div className="content-editor">
      {/* Header del editor */}
      <div className="editor-header">
        <div className="editor-progress">
          <div className="progress-label">
            Progreso de traducción: {calculateTranslationProgress()}%
          </div>
          <div className="progress-bar">
            <div 
              className="progress-fill" 
              style={{ width: `${calculateTranslationProgress()}%` }}
            ></div>
          </div>
        </div>
        
        <div className="publish-status">
          {publishStatus[currentLanguage] ? (
            <div className="status published">
              <i className="fas fa-check-circle"></i>
              <span>Publicado</span>
            </div>
          ) : (
            <div className="status draft">
              <i className="fas fa-pen"></i>
              <span>Borrador</span>
            </div>
          )}
        </div>
      </div>
      
      {/* Mensajes de estado */}
      {message && (
        <div className={`message ${message.type}`}>
          <i className={`fas fa-${message.type === 'success' ? 'check' : message.type === 'warning' ? 'exclamation' : 'times'}-circle`}></i>
          <span>{message.text}</span>
          <button 
            className="message-close"
            onClick={() => setMessage(null)}
          >
            ×
          </button>
        </div>
      )}
      
      {/* Selector de idioma */}
      <div className="language-tabs">
        {availableLanguages.map(lang => (
          <button
            key={lang}
            className={`language-tab ${lang === currentLanguage ? 'active' : ''} ${content[lang] ? 'has-content' : ''} ${publishStatus[lang] ? 'published' : ''}`}
            onClick={() => setCurrentLanguage(lang)}
          >
            {languageNames[lang]}
            {publishStatus[lang] && <i className="fas fa-check-circle"></i>}
          </button>
        ))}
      </div>
      
      {/* Editor de contenido */}
      <div className="editor-container">
        <div className="editor-wrapper">
          <ReactQuill
            value={content[currentLanguage] || ''}
            onChange={handleContentChange}
            modules={editorModules}
            theme="snow"
            placeholder={`Escribe tu contenido en ${languageNames[currentLanguage]}...`}
          />
        </div>
      </div>
      
      {/* Sección SEO */}
      <div className="seo-section">
        <h3 className="seo-title">
          <i className="fas fa-search"></i>
          <span>SEO para {languageNames[currentLanguage]}</span>
        </h3>
        
        <div className="seo-fields">
          <div className="form-group">
            <label htmlFor="seo-title">Título SEO:</label>
            <input
              type="text"
              id="seo-title"
              value={seoData[currentLanguage]?.title || ''}
              onChange={(e) => handleSeoChange('title', e.target.value)}
              placeholder="Título para motores de búsqueda"
              maxLength={60}
            />
            <div className="character-count">
              {(seoData[currentLanguage]?.title?.length || 0)}/60
            </div>
          </div>
          
          <div className="form-group">
            <label htmlFor="seo-description">Descripción SEO:</label>
            <textarea
              id="seo-description"
              value={seoData[currentLanguage]?.description || ''}
              onChange={(e) => handleSeoChange('description', e.target.value)}
              placeholder="Descripción para motores de búsqueda"
              maxLength={160}
              rows={3}
            ></textarea>
            <div className="character-count">
              {(seoData[currentLanguage]?.description?.length || 0)}/160
            </div>
          </div>
          
          <div className="form-group">
            <label htmlFor="seo-keywords">Palabras clave SEO:</label>
            <input
              type="text"
              id="seo-keywords"
              value={seoData[currentLanguage]?.keywords || ''}
              onChange={(e) => handleSeoChange('keywords', e.target.value)}
              placeholder="Palabras clave separadas por comas"
            />
          </div>
        </div>
      </div>
      
      {/* Versiones */}
      {versions[currentLanguage] && versions[currentLanguage].length > 0 && (
        <div className="versions-section">
          <h3 className="versions-title">
            <i className="fas fa-history"></i>
            <span>Historial de versiones</span>
          </h3>
          
          <div className="versions-list">
            {versions[currentLanguage]
              .sort((a, b) => b.version - a.version)
              .map(version => (
                <div 
                  key={version.id} 
                  className={`version-item ${version.version === currentVersions[currentLanguage] ? 'current' : ''}`}
                >
                  <div className="version-info">
                    <span className="version-number">v{version.version}</span>
                    <span className="version-date">
                      {new Date(version.updated_at).toLocaleString()}
                    </span>
                    {version.published && (
                      <span className="version-published">
                        <i className="fas fa-check-circle"></i>
                        <span>Publicada</span>
                      </span>
                    )}
                  </div>
                  
                  <button className="version-view-button">
                    <i className="fas fa-eye"></i>
                    <span>Ver</span>
                  </button>
                </div>
              ))}
          </div>
        </div>
      )}
      
      {/* Acciones */}
      <div className="editor-actions">
        <button 
          className="save-button"
          onClick={saveContent}
          disabled={isSaving}
        >
          {isSaving ? (
            <>
              <div className="button-spinner"></div>
              <span>Guardando...</span>
            </>
          ) : (
            <>
              <i className="fas fa-save"></i>
              <span>Guardar</span>
            </>
          )}
        </button>
        
        {publishStatus[currentLanguage] ? (
          <button 
            className="unpublish-button"
            onClick={unpublishCurrentLanguage}
            disabled={isSaving}
          >
            <i className="fas fa-eye-slash"></i>
            <span>Despublicar</span>
          </button>
        ) : (
          <button 
            className="publish-button"
            onClick={publishCurrentLanguage}
            disabled={isSaving || !content[currentLanguage]}
          >
            <i className="fas fa-globe"></i>
            <span>Publicar</span>
          </button>
        )}
        
        <button className="preview-button" disabled={!content[currentLanguage]}>
          <i className="fas fa-eye"></i>
          <span>Vista previa</span>
        </button>
      </div>
    </div>
  );
};

export default ContentEditor;
