// frontend/src/components/ui/Toast.jsx
import React, { useState, useEffect } from 'react';
import '../../assets/css/components/toast.css';

/**
 * Componente Toast para mostrar notificaciones
 * @param {Object} props - Propiedades del componente
 * @param {string} props.message - Mensaje a mostrar
 * @param {string} props.type - Tipo de toast (success, error, warning, info)
 * @param {number} props.duration - Duración en ms (por defecto 3000)
 * @param {function} props.onClose - Función a ejecutar al cerrar el toast
 */
const Toast = ({ message, type = 'info', duration = 3000, onClose }) => {
  const [visible, setVisible] = useState(true);
  
  useEffect(() => {
    // Ocultar automáticamente después de la duración especificada
    const timer = setTimeout(() => {
      setVisible(false);
      if (onClose) setTimeout(onClose, 300); // Esperar a que termine la animación
    }, duration);
    
    return () => clearTimeout(timer);
  }, [duration, onClose]);
  
  // Manejar cierre manual
  const handleClose = () => {
    setVisible(false);
    if (onClose) setTimeout(onClose, 300);
  };
  
  // Determinar icono según el tipo
  const getIcon = () => {
    switch (type) {
      case 'success':
        return 'check-circle';
      case 'error':
        return 'times-circle';
      case 'warning':
        return 'exclamation-circle';
      case 'info':
      default:
        return 'info-circle';
    }
  };
  
  return (
    <div className={`toast toast-${type} ${visible ? 'toast-visible' : 'toast-hidden'}`}>
      <div className="toast-content">
        <i className={`fas fa-${getIcon()}`}></i>
        <span className="toast-message">{message}</span>
      </div>
      <button className="toast-close" onClick={handleClose}>
        <i className="fas fa-times"></i>
      </button>
    </div>
  );
};

export default Toast;
