// frontend/src/App.js - Base React para landing page
import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { LanguageProvider } from './contexts/LanguageContext';
import { AuthProvider } from './contexts/AuthContext';
import { ToastProvider } from './contexts/ToastContext';
import ProtectedRoute from './components/ProtectedRoute';
import PageViewTracker from './components/analytics/PageViewTracker';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import RegisterPage from './pages/RegisterPage';
import LoginPage from './pages/LoginPage';
import AdminLayout from './components/admin/AdminLayout';
import AdminDashboard from './pages/admin/AdminDashboard';
import ContentManagement from './pages/admin/ContentManagement';
import EarlyAdoptersManager from './pages/admin/EarlyAdoptersManager';
import SocialMediaManager from './pages/admin/SocialMediaManager';
import './assets/css/main.css';

function App() {
  return (
    <LanguageProvider>
      <AuthProvider>
        <ToastProvider>
          <BrowserRouter basename="/nueva">
            {/* Componente para rastrear vistas de página */}
            <PageViewTracker />
            
            <Routes>
              {/* Rutas públicas */}
              <Route path="/" element={<HomePage />} />
              <Route path="/about" element={<AboutPage />} />
              <Route path="/register" element={<RegisterPage />} />
              <Route path="/login" element={<LoginPage />} />
              
              {/* Rutas protegidas de administración */}
              <Route path="/admin" element={
                <ProtectedRoute>
                  <AdminLayout />
                </ProtectedRoute>
              }>
                <Route index element={<AdminDashboard />} />
                <Route path="content" element={<ContentManagement />} />
                <Route path="early-adopters" element={<EarlyAdoptersManager />} />
                <Route path="social" element={<SocialMediaManager />} />
              </Route>
            </Routes>
          </BrowserRouter>
        </ToastProvider>
      </AuthProvider>
    </LanguageProvider>
  );
}

export default App;
