// frontend/src/components/analytics/PageViewTracker.jsx
import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import analyticsService from '../../services/analyticsService';

/**
 * Componente para rastrear vistas de página automáticamente
 * Se integra con React Router para detectar cambios de ruta
 */
const PageViewTracker = () => {
  const location = useLocation();
  
  useEffect(() => {
    // Enviar vista de página al servicio de analytics
    analyticsService.trackPageView(location.pathname + location.search);
  }, [location]);
  
  // Este componente no tiene render, solo es funcional
  return null;
};

export default PageViewTracker;
