// frontend/src/pages/LoginPage.jsx
import React, { useState, useContext } from 'react';
import { Navigate, useNavigate, useLocation } from 'react-router-dom';
import { AuthContext } from '../contexts/AuthContext';
import SEOHead from '../components/SEOHead';

const LoginPage = () => {
  const [credentials, setCredentials] = useState({
    email: '',
    password: '',
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  
  const { isAuthenticated, login } = useContext(AuthContext);
  const navigate = useNavigate();
  const location = useLocation();
  
  // Redirigir a la página que intentaban acceder o al dashboard
  const from = location.state?.from?.pathname || "/admin";
  
  // Si ya está autenticado, redirigir
  if (isAuthenticated) {
    return <Navigate to={from} />;
  }
  
  const handleChange = (e) => {
    setCredentials({
      ...credentials,
      [e.target.name]: e.target.value,
    });
  };
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    
    try {
      const result = await login(credentials.email, credentials.password);
      
      if (result.success) {
        navigate(from, { replace: true });
      } else {
        setError(result.message || 'Error al iniciar sesión');
      }
    } catch (err) {
      setError('Error al conectar con el servidor');
    } finally {
      setLoading(false);
    }
  };
  
  return (
    <div className="login-page">
      <SEOHead 
        title="Iniciar Sesión | Adsum-Blockchain Admin"
        description="Accede al panel de administración de Adsum-Blockchain"
        language="es"
        canonical="https://adsum-blockchain.com/login"
      />
      
      <div className="login-container">
        <div className="login-logo">
          <img src="/static/img/logo.png" alt="Adsum Blockchain" />
          <h1>Adsum-Blockchain</h1>
        </div>
        
        <div className="login-form-container">
          <h2>Iniciar Sesión</h2>
          <p>Accede al panel de administración</p>
          
          {error && (
            <div className="error-message">
              {error}
            </div>
          )}
          
          <form onSubmit={handleSubmit} className="login-form">
            <div className="form-group">
              <label htmlFor="email">Email</label>
              <input
                type="email"
                id="email"
                name="email"
                value={credentials.email}
                onChange={handleChange}
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="password">Contraseña</label>
              <input
                type="password"
                id="password"
                name="password"
                value={credentials.password}
                onChange={handleChange}
                required
              />
            </div>
            
            <button 
              type="submit" 
              className="login-button"
              disabled={loading}
            >
              {loading ? 'Iniciando sesión...' : 'Iniciar Sesión'}
            </button>
          </form>
          
          <div className="login-footer">
            <a href="#">¿Olvidaste tu contraseña?</a>
            <a href="/">Volver al sitio principal</a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
