// frontend/src/services/api.js
/**
 * Servicio centralizado para comunicación con la API
 * Incluye funciones para todas las operaciones del CMS
 */

// URLs base para la API
const API_URL = process.env.NODE_ENV === 'production' 
  ? '/api' 
  : 'http://localhost:8000/api';

// Opciones comunes para fetch
const commonOptions = {
  headers: {
    'Content-Type': 'application/json',
  },
};

// Función utilitaria para manejar errores de las peticiones
const handleApiError = async (response) => {
  if (!response.ok) {
    const errorData = await response.json().catch(() => ({}));
    throw new Error(errorData.detail || 'Ha ocurrido un error en la API');
  }
  return response.json();
};

// Función para añadir token de autenticación a las opciones (para rutas protegidas)
const getAuthOptions = (options = {}) => {
  const token = localStorage.getItem('auth_token');
  
  if (!token) {
    return options;
  }
  
  return {
    ...options,
    headers: {
      ...options.headers,
      Authorization: `Bearer ${token}`,
    },
  };
};

// Servicios para gestión de early adopters
export const earlyAdoptersService = {
  // Registrar un nuevo early adopter
  register: async (data) => {
    const response = await fetch(`${API_URL}/early-adopters`, {
      ...commonOptions,
      method: 'POST',
      body: JSON.stringify(data),
    });
    
    return handleApiError(response);
  },
  
  // Obtener todos los early adopters
  getAll: async (filters = {}) => {
    const params = new URLSearchParams();
    
    // Añadir filtros a los parámetros de consulta
    Object.entries(filters).forEach(([key, value]) => {
      if (value !== undefined && value !== null) {
        params.append(key, value);
      }
    });
    
    const queryString = params.toString() ? `?${params.toString()}` : '';
    const response = await fetch(`${API_URL}/early-adopters${queryString}`, getAuthOptions());
    
    return handleApiError(response);
  },
  
  // Obtener un early adopter por ID
  getById: async (id) => {
    const response = await fetch(`${API_URL}/leads/${id}`, getAuthOptions());
    
    return handleApiError(response);
  },
  
  // Otorgar una insignia a un early adopter
  awardBadge: async (leadId, badgeType) => {
    const response = await fetch(`${API_URL}/badges`, {
      ...getAuthOptions(commonOptions),
      method: 'POST',
      body: JSON.stringify({
        lead_id: leadId,
        badge_type: badgeType,
      }),
    });
    
    return handleApiError(response);
  },
  
  // Obtener insignias de un early adopter
  getBadges: async (leadId) => {
    const response = await fetch(`${API_URL}/leads/${leadId}/badges`, getAuthOptions());
    
    return handleApiError(response);
  },
};

// Servicios para gestión de contenido
export const contentService = {
  // Crear o actualizar contenido
  createOrUpdate: async (contentData) => {
    const response = await fetch(`${API_URL}/content`, {
      ...getAuthOptions(commonOptions),
      method: 'POST',
      body: JSON.stringify(contentData),
    });
    
    return handleApiError(response);
  },
  
  // Obtener contenido por clave y lenguaje
  getByKey: async (key, language = 'en', version = null) => {
    let url = `${API_URL}/content/${key}?language=${language}`;
    
    if (version) {
      url += `&version=${version}`;
    }
    
    const response = await fetch(url);
    
    return handleApiError(response);
  },
  
  // Obtener todas las versiones de un contenido
  getAllVersions: async (key) => {
    const response = await fetch(`${API_URL}/content/${key}/all`);
    
    return handleApiError(response);
  },
  
  // Publicar una versión específica de contenido
  publish: async (contentId) => {
    const response = await fetch(`${API_URL}/content/${contentId}/publish`, {
      ...getAuthOptions(commonOptions),
      method: 'PUT',
    });
    
    return handleApiError(response);
  },
};

// Servicios para gestión de cuentas sociales
export const socialService = {
  // Crear nueva cuenta social
  createAccount: async (accountData) => {
    const response = await fetch(`${API_URL}/social/accounts`, {
      ...getAuthOptions(commonOptions),
      method: 'POST',
      body: JSON.stringify(accountData),
    });
    
    return handleApiError(response);
  },
  
  // Obtener cuentas sociales
  getAccounts: async (platform = null) => {
    let url = `${API_URL}/social/accounts`;
    
    if (platform) {
      url += `?platform=${platform}`;
    }
    
    const response = await fetch(url, getAuthOptions());
    
    return handleApiError(response);
  },
  
  // Crear publicación programada
  createPost: async (postData) => {
    const response = await fetch(`${API_URL}/social/posts`, {
      ...getAuthOptions(commonOptions),
      method: 'POST',
      body: JSON.stringify(postData),
    });
    
    return handleApiError(response);
  },
  
  // Obtener publicaciones programadas
  getScheduledPosts: async () => {
    const response = await fetch(`${API_URL}/social/posts/scheduled`, getAuthOptions());
    
    return handleApiError(response);
  },
  
  // Actualizar estado de publicación
  updatePostStatus: async (postId, status, postUrl = null, metrics = null) => {
    const response = await fetch(`${API_URL}/social/posts/${postId}/status`, {
      ...getAuthOptions(commonOptions),
      method: 'PUT',
      body: JSON.stringify({
        status,
        post_url: postUrl,
        metrics,
      }),
    });
    
    return handleApiError(response);
  },
};

// Servicios para autenticación
export const authService = {
  // Iniciar sesión
  login: async (email, password) => {
    const response = await fetch(`${API_URL}/auth/login`, {
      method: 'POST',
      headers: {
	'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: new URLSearchParams({
	username: email,
	password: password,
      }),
    });
    
    const data = await handleApiError(response);
    
    // Guardar el token en localStorage
    if (data.access_token) {
      localStorage.setItem('auth_token', data.access_token);
    }
    
    return data;
  },
  
  // Cerrar sesión
  logout: () => {
    localStorage.removeItem('auth_token');
  },
  
  // Verificar si el usuario está autenticado
  isAuthenticated: () => {
    return !!localStorage.getItem('auth_token');
  },
};

// Servicios para obtener archivos de idioma
export const languageService = {
  getTranslations: async (lang) => {
    const response = await fetch(`${API_URL}/languages/${lang}`);
    
    return handleApiError(response);
  },
};

// Exportar todos los servicios
const apiService = {
  earlyAdopters: earlyAdoptersService,
  content: contentService,
  social: socialService,
  auth: authService,
  language: languageService,
};

export default apiService;
